<template>
	<div @mouseleave="active=false" @mouseover="active=true" class="works__item-box">
		<div @mouseover="active=true" v-show="active==true" class="works__item-description">
			<div class="works__item-title">
				<span class="works__item-header">Awesome Day</span>
				<span class="works__item-text">View from above</span>
			</div>
			<div class="works__item-date">
				<span class="works__item-date-month">JAN</span>
				<span class="works__item-date-day">25th</span>
			</div>
			
		</div>
		<img @mouseover="active=true" class="works__item-image works__image-highlighted" :src="url" :alt="title">
		<div v-show="active" class="works__item-action">
			<div class="works__item-action-left">
				<div class="works__item-action-icon-box">
					<i class="fas fa-heart"></i>
					<span>{{ likes }}</span>
				</div>
				<div class="works__item-action-icon-box">
					<i class="fas fa-comment-alt"></i>
					<span>{{ comments }}</span>
				</div>
			</div>
			<div class="works__item-action-right">
				<i class="fas fa-compress"></i>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'WorkItem',
		props:['title', 'url', 'likes', 'comments'],
		data() {
			return {
				active: false,
			}
		}
	}
</script>

<style>
	.works__item-box {
		width: 266px;
		height: 266px;
		border: 4px solid #fff;
		box-sizing: border-box;
		margin-bottom: 34px;
		position: relative;
		display: flex;
		flex-direction: column;
	}
	.works__item-image {
		max-width: 258px;
		max-height: 258px;
	}
/*	.works__image-highlighted {
		max-height: 190px;
	}*/

	/*indent*/


	.works__item-date-month {
		display: block;
		font-size: 14px;
	}
	.works__item-date-day {
		display: block;
		font-size: 13px;
	  	text-align: center;
	}


	/*indent*/


	.works__item-description {
		position: absolute;
		width: 100%;
		top: 0;
		left: 0;
		background-color: rgba(68, 46, 42, 0.8);
		color: #fff;
		display: flex;
		justify-content: space-between;
	}
	.works__item-action {
		padding: 22px 0;
		background-color: #fff;
		display: flex;
		justify-content: space-between;
	}
	.works__item-title {
		margin-left: 10px;
		padding: 10px;
	}
	.works__item-header {
	  font-size: 14px;
	  font-weight: bold;
	  display: block;
	}
	.works__item-text {
	  display: block;
	  font-size: 13px;

	}
	.works__item-date {
		margin-right: 10px;
		padding: 10px 0;
	}
	

	/*indent*/

	.works__item-action {
		position: absolute;
		left: 0;
		bottom: 0;
		width: 100%
	}
	.works__item-action-left {
		display: flex;
	}
	.works__item-action-icon-box {
		margin-left: 10px
	}
	.works__item-action-icon-box i {
		color: #e6618c;
		font-size: 14px;
		cursor: pointer;
		
	}
	.works__item-action-icon-box span {
		color: #9f9d9b;
		font-size: 14px;
	}
	.works__item-action-right {
		margin-right: 10px;
		cursor: pointer;
	}
</style>


