<template>
	<div class="post__wrap">
		<div class="post__header">
			<span class="">A Picture Is Worth A Thousand Words</span>
			<span>01/17/2014</span>
		</div>
		<div class="post__content">
			<div class="post__item-box">
				<a href="/pricing/postpage/123">
					<router-link to="/pricing/postpage/123"><h3>A Picture Is Worth A Thousand Words</h3></router-link>
				</a>
				
				<div class="post__item">
					<div class="post__item-image">
						<img src="http://img1.joyreactor.cc/pics/post/full/%D0%90%D0%B2%D1%81%D1%82%D1%80%D0%B0%D0%BB%D0%B8%D1%8F-%D1%81%D1%82%D1%80%D0%B0%D0%BD%D1%8B-%D0%BD%D0%B5%D0%B2%D0%B5%D0%B4%D0%BE%D0%BC%D1%8B%D0%B5-%D1%82%D0%B2%D0%B0%D1%80%D0%B8-%D0%BA%D1%80%D0%B8%D0%BF%D0%BE%D1%82%D0%B0-5408251.jpeg" alt="">
						<span>Flight over the country - 2014</span>
					</div>
					<div class="post__item-text">
						<p class="post__item-text_bold">Dad blizzard coffee unsave facepalm face feel like a sir tank mother aww yeah. Anonymous easter puking rainbows bear wodka simpson amnesia read cookie monster final week grey.</p> 

						<p>Mom hammer creepy me gusta morbi eat le friend steve jobs cellphone clinton cereal guy. Good guy high school on okay a bacon homework in joke dog. Not Okay if you know what i mean nap monocle oh stop it, you pizza diablo 3 captain on weekend elephant. I Dont Get It drink ba dumm tss amet basic math female phone gentlemen strangers.</p>
					</div>
				</div>
			</div>
			<div class="post__item-author-box">
				<div class="post__item-author-data">
					<h3>Author</h3>
					<span>Jonas Duri</span>
					<img class="post__item-author-avatar" src="https://i.redd.it/nq06e6zurzcz.png" alt="">
					
				</div>
				<ul class="post__social">
					<li  v-for="item in socials" :key="item.name"><a  v-bind:href="item.link" v-html="item.icon"></a></li>
				</ul>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'BlogPost',
		data() {
			return {
				socials: [
					{name: 'facebook', icon: '<i class="fab fa-facebook-f"></i>', link: 'http://facebook.com'},
					{name: 'twitter', icon: '<i class="fab fa-twitter"></i>', link: 'http://twitter.com'},
					{name: 'instagram', icon: '<i class="fab fa-instagram"></i>', link: 'http://instagram.com'},
					{name: 'youtube', icon: '<i class="fab fa-youtube"></i>', link: 'http://youtube.com'},
					{name: 'vimeo', icon: '<i class="fab fa-vimeo-v"></i>', link: 'http://vimeo.com'},
				],
			}
		}
	}
</script>

<style>
		.post__wrap {
		padding: 50px 30px;
		background-color: #fff;
		margin-bottom: 28px;
		text-align: left;
	}
	@media screen and (max-width: 944px) {
		.post__wrap {
			text-align: center;
		}
	}
	@media screen and (max-width: 767px) {
		.post__wrap {
			padding: 50px 0;
		}
	}	
	.post__header {
		color: rgb(152, 155, 159);
		font-size: 14px;
		line-height: 2.143;
		/*text-align: left;*/
		margin-bottom: 15px
	}
	.post__header span {
		margin: 10px 20px 10px 0;

	}
	@media screen and (max-width: 520px) {
		.post__header span {
			display: block;
			margin: auto;
		}
	}
	.post__content {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
	}
	@media screen and (max-width: 1092px) {
		.post__content {
			flex-direction: column;
		}
	}
	.post__item-box {
		display: flex;
		flex-direction: column;
	}
	.post__item-box h3 {
		font-size: 28px;
		font-family: "YanoneKaffeesatz";
		color: rgb(0, 0, 0);
		line-height: 1.071;
		/*text-align: left;*/
		margin-bottom: 27px;
	}
	.post__item {
		display: flex;
		flex-direction: row;
	}
	@media screen and (max-width: 944px) {
		.post__item {
			flex-direction: column;
			align-items: center;
		}
	}
	.post__item-image {
		max-width: 235px;
		max-height: 235px;
		
		display: flex;
		flex-direction: column;
	}
	.post__item-image img {
		max-width: 222px;
		max-height: 232px;
		box-sizing: border-box;
		border: 5px solid #f0f0f0;
		
	}
	.post__item-image span {
		font-size: 12px;
		line-height: 1.5;
		color: rgb(152, 155, 159);
		margin: 18px 0;
	}

	.post__item-text {
		margin: 0 30px;
	}
	.post__item-text p {
		font-size: 17px;
		color: rgb(152, 155, 159);
		font-family: "Arial";
		line-height: 1.412;
		/*text-align: left;*/
		max-width: 388px;
	}
	@media screen and (max-width: 991px) {
		.post__item-text p {
			max-width: 100%;
		}
	}
	p.post__item-text_bold {
		font-weight: bold;
		color: black;
	}

	/*indent*/
	.post__item-author-box {
		text-align: center;
	}
	@media screen and (max-width: 1092px) {
		.post__item-author-box {
			width: 80%
		}
	}
	.post__item-author-data {
		text-align: center;
	}
	.post__item-author-data h3 {
		font-size: 20px;
		font-family: "YanoneKaffeesatz";
		color: #000;
		line-height: 1.2;
		margin-bottom: 0
	}
	.post__item-author-data span {
		font-size: 12px;
		font-family: "Arial";
		color: rgb(230, 97, 140);
		line-height: 1.5;
		display: block;
	}
	.post__item-author-avatar {
		border-radius: 50%;
		width: 48px;
		height: 48px;
		margin: 16px 0
	}
	.post__social {
		list-style: none;
		padding-inline-start: unset;
		margin-top: 25px;
	}
	@media screen and (max-width: 1092px) {
		.post__social {
			display: flex;
			justify-content: space-between;
			width: 100%;
		}
	}
	.post__social li {
		margin-bottom: 18px;
	}
	.post__social a {
		color: #c8c8c8;
	}
	.post__social a:hover {
		color: #000;
	}
	.post__social i {
		font-size: 18px;
	}


</style>


