<template>
	<header class="container-fluid ">
		<hidden-menu ></hidden-menu>
<!-- 		<div :class="menuState == true ? 'visible_menu' : ''" class="header__hidden-menu">
			<div @click="menuState=!menuState" class="header__close-menu ">
				<i class="fas fa-times"></i>
			</div>
			<ul class="navbar-nav">
				<li class="nav-item" v-for="item in socials" :key="item.name"><a class="nav-link" v-bind:href="item.link">{{ item.name }}</a></li>
			</ul>
		</div> -->
		<div class="row m-auto wrapper">
			<div class="col-lg-3 col-md-12">
				<ul class="header__social ">
					<li  v-for="item in socials" :key="item.name"><a  v-bind:href="item.link" v-html="item.icon"></a></li>
				</ul>
			</div>
			<div class="col-lg-2 offset-lg-2 col-md-12">
				<div>
					<a href="/pricing"><img class="gravity" src="@/assets/static/Logo-AgencyFB-footer.png" alt=""></a>
				</div>
			</div>
			<div class="col-lg-1 offset-lg-4 col-md-12">
				<div  class="header__nav-bar">
					<i @click="switchTest" v-if="menuState==false" class="fas fa-bars header__nav-bar-button"></i>
				</div>
			</div>
		</div>
	</header>
</template>

<script>
	export default {
		name: 'FooterBlock',
		data() {
			return {
				socials: [
					{name: 'facebook', icon: '<i class="fab fa-facebook-f"></i>', link: 'http://facebook.com'},
					{name: 'twitter', icon: '<i class="fab fa-twitter"></i>', link: 'http://twitter.com'},
					{name: 'instagram', icon: '<i class="fab fa-instagram"></i>', link: 'http://instagram.com'},
					{name: 'youtube', icon: '<i class="fab fa-youtube"></i>', link: 'http://youtube.com'},
					{name: 'vimeo', icon: '<i class="fab fa-vimeo-v"></i>', link: 'http://vimeo.com'},
				],
				// test: this.$store.getters.tat
			}
		},
		methods: {
			switchTest() {
				this.$store.commit('switchTest')
			},
		},
		computed: {
			menuItems() {
				return this.$store.getters.getMenuItems
			},
			menuState() {
				return this.$store.getters.getMenuState
			}

		}
	}

// $(document).ready(function(){
// 	$('body').on('click', function(e){
// 		if(e.target.className == 'fas fa-bars header__nav-bar-button') {
// 			$('.header__hidden-menu').addClass('visible_menu')
// 			$('.header__nav-bar-button').hide()
// 		}
// 	});
// })
// hideMenu = function() {
// 		$('body').on('click', function(e){
// 			if(e.target.className != 'visible_menu') {
// 				$('.header__hidden-menu').removeClass('visible_menu')
// 				$('.header__nav-bar-button').show()
// 				console.log(e.target.className)
// 			}
// 		})
// 	}
</script>

<style scoped>
	.header__social {
		display: flex;
		justify-content: space-between;
		align-items: center;
		list-style-type: none;
		padding: 0
	}
	@media screen and (max-width: 991px) {
		.header__social {
			max-width: 70%;
			margin: auto;
			margin-bottom: 20px;
		}
	}
	.header__social li {
		margin-right: 5px;
		color: #a0a1a5;
	}
	.header__social i {
		font-size: 21px;
		color: #a0a1a5;
	}
	.header__social a {
		color: #a0a1a5;
	}
	.header__social a:hover {
		color: #000;
	}
	header {
		padding: 60px 0;
		background-color: #202020;
		overflow: hidden;
	}
	.header__nav-bar {
		cursor: pointer;
		opacity: 1;
	}
	.header__nav-bar i {
		color: #fff;
	}
	@media screen and (max-width: 991px) {
		.header__nav-bar {
			margin-top: 38px;
		}
	}
	.header__hidden-menu {
		height: 100vh;
		position: fixed;
		right: 0;
		top: 0;
		width: 40%;
		background-color: rgba(68, 46, 42, 0.8);
		padding: 30px;
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		transform: translateX(100%);
		/*display: none;*/
		transition-duration: 1s;
	}
	@media screen and (max-width: 767px) {
		.header__hidden-menu {
			width: 70%;
		}
	}
	.header__hidden-menu ul {
		padding: 40px 20px
	}
	.header__hidden-menu a {
		color: #fff;
		font-size: 21px;
		font-weight: bold;
	}
	.header__hidden-menu a:hover {
		color: #000;
		transform: translateY(-2px)
	}
	.visible_menu {
		transform: translateX(0%);
		transition-duration: 1s;
		/*display: ;*/
	}

	.header__nav-bar-button {
		font-size: 21px;
	}
	.header__nav-bar-button_hidden {
		display: none;
	}
	.header__close-menu {
		position: absolute;
		top: 2%;
		left: 6%;
		cursor: pointer;
	}
	.header__close-menu i {
		font-size: 26px;
		color: #fff;
	}
	.header__close-menu i:hover {
		color: #000;
	}
</style>


