<template>
	<div :class="menuState == true ? 'visible_menu' : ''" class="header__hidden-menu" >
		<div @click="switchTest" class="header__close-menu ">
			<i class="fas fa-times"></i>
		</div>
<!-- 		<ul class="navbar-nav">
			<li class="nav-item" v-for="item in menuItems" :key="item.name"><a class="nav-link" v-bind:href="item.link">{{ item.name }}</a></li>
		</ul> -->
		<ul class="navbar-nav">
			<li class="nav-item" ><router-link to="/pricing/">Home</router-link></li>
			<li class="nav-item" ><router-link to="/pricing/posts">Posts</router-link></li>
			<li class="nav-item" ><router-link to="/pricing/postpage/123">Single post</router-link></li>
		</ul>
	</div>
</template>

<script>
	export default {
		// props: {test: Boolean},
		name: 'HiddenMenu',
		data() {
			return {
				
			}
		},
		methods: {
			switchTest() {
				this.$store.commit('switchTest')
			},
		},
		computed: {
			menuItems() {
				return this.$store.getters.getMenuItems
			},
			menuState() {
				return this.$store.getters.getMenuState
			}

		}
	}
</script>

<style>
		.header__hidden-menu {
		height: 100vh;
		position: fixed;
		right: 0;
		top: 0;
		width: 40%;
		background-color: rgba(68, 46, 42, 0.8);
		padding: 30px;
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		transform: translateX(100%);
		transition-duration: 1s;
	}
	@media screen and (max-width: 767px) {
		.header__hidden-menu {
			width: 70%;
		}
	}
	.header__hidden-menu ul {
		padding: 40px 20px
	}
	.header__hidden-menu a {
		color: #fff;
		font-size: 21px;
		font-weight: bold;
	}
	.header__hidden-menu a:hover {
		color: #000;
		transform: translateY(-2px)
	}
	.visible_menu {
		transform: translateX(0%);
		transition-duration: 1s;
		/*display: ;*/
	}
	.hide_menu {
		transform: translateX(100%);
		transition-duration: 1s;
		/*display: ;*/
	}
	.header__nav-bar-button {
		font-size: 21px;
	}
	.header__nav-bar-button_hidden {
		display: none;
	}
	.header__close-menu {
		position: absolute;
		top: 2%;
		left: 6%;
		cursor: pointer;
	}
	.header__close-menu i {
		font-size: 26px;
		color: #fff;
	}
	.header__close-menu i:hover {
		color: #000;
	}
</style>


